$btn-control-round-diameter-lg: 60px;
$btn-control-round-diameter-sm: 40px;
$btn-control-margin: 5px;

.btn-control-round {
  width: $btn-control-round-diameter-sm;
  height: $btn-control-round-diameter-sm;
  border: none;
  padding: 0;
  border-radius: 50%;
  margin: $btn-control-margin;
  background-color: $c-green;
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  font-size: 0;

  &:hover {
    background-color: $c-green !important;
  }
}

.btn-control-round-lg {
  width: $btn-control-round-diameter-lg;
  height: $btn-control-round-diameter-lg;
}

.btn-control-round-lg.btn-play {
  background-image: url('../../vendor/font-awesome/play-solid.svg');
  background-position: $btn-control-round-diameter-lg * 0.5 * 0.5 + 2px $btn-control-round-diameter-lg * 0.5 * 0.5 - 5px;
}

.btn-control-round.btn-stop {
  background-image: url('../../vendor/font-awesome/stop-solid.svg');
}

.btn-control-round.btn-pause {
  background-image: url('../../vendor/font-awesome/pause-solid.svg');
}

.btn-control-round-clear {
  background-image: url('../../vendor/font-awesome/trash-can-solid.svg');
}

.muski-tempo {
  display: inline-block;

  .muski-tempo-label {
    text-transform: uppercase;
    font-size: 0.85em;
    font-weight: bold;
    background-color: $c-yellow;
    padding: 0.2em 0.6em;
  }

  .muski-tempo-range {
    width: 200px;
    vertical-align: middle;
  }

  .muski-tempo-display {
    display: inline-block;
    min-width: 5em;
  }
}

.muski-drums-controls-panel {
  display: flex;
  align-items: center;
  padding-left: 8em;
  width: 840px;

  .btn-control-round-clear {
    margin-left: auto;
    margin-right: -3px;
  }
}
